import { useParams } from "react-router-dom";
import { useForm }   from "react-hook-form";

import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import AppraisalSection4           from "./AppraisalSection4";

const PARAMS =  { select_fields : ["page_four"] };
const DEFAULT_FETCH_DATA = { page_four : {} };


const AppraisalSection4Container = ({ appraise = DEFAULT_FETCH_DATA }) => {
	const { id : appraiseId } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { page_four } = appraise;

	// ------------> hookForm <---------------
	const defaultValues = { ...page_four };

	const hookForm = useForm({ defaultValues });

	const { handleSubmit } = hookForm;

	const onSubmit =  handleSubmit(async (formData) => {
		await editApprasial({
			module : `/appraisals/page-four/${appraiseId}`,
			method : "PUT",
			data   : formData,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<AppraisalSection4
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(AppraisalSection4Container, PARAMS);

