import {
	Tabs,
	Surfaces,
	HeaderPage,
	DescGeneral,
} from "Components/Appraisal";

const tabs = [
	{
		title        : "Descripción General de las Construcciones",
		Tab          : DescGeneral,
		sectionTitle : "2.2 Descripción General de las Construcciones",
	},
	{
		title        : "Superficies",
		Tab          : Surfaces,
		sectionTitle : "2.3 Superficies",
	},
];

const AppraisalSection4 = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[1, 1]}
				section="body"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			 />
			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			 />
		</>
	);
};

export default AppraisalSection4;
